body {
  font-family: "League Spartan", sans-serif;
  font-size: 1.5rem;
  color: hsl(180deg, 8%, 52%);
  background-color: hsl(180deg, 52%, 96%);
}

p {
  margin: 0;
  line-height: 1.7;
}

.contenedor {
  padding: 0 2.4rem;
  max-width: 1110px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor {
    padding: 0 3.2rem;
  }
}
@media (min-width: 1200px) {
  .contenedor {
    padding: 0;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h2, h3 {
  margin: 0;
  font-family: "League Spartan", sans-serif;
  margin-bottom: 1.5rem;
  line-height: 1.5;
}

h2 {
  font-size: 1.8rem;
  color: hsl(180deg, 14%, 20%);
  margin-bottom: 0.5rem;
}

h3 {
  font-size: 1.8rem;
  color: hsl(180deg, 29%, 50%);
}

button {
  color: hsl(180deg, 29%, 50%);
  background-color: hsl(180deg, 52%, 96%);
  padding: 0.7rem 1rem;
  font-weight: 700;
  text-align: center;
  border: none;
  border-radius: 0.7rem;
}
button:hover {
  cursor: pointer;
}
button.tag {
  transition: all 0.3s;
}
button.tag:hover {
  background-color: hsl(180deg, 29%, 50%);
  color: #fff;
}

.trabajos .hero {
  background-image: url(../img/bg-header-mobile.svg);
  background-color: hsl(180deg, 29%, 50%);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 15rem;
}
@media (min-width: 992px) {
  .trabajos .hero {
    background-image: url(../img/bg-header-desktop.svg);
    height: 15.6rem;
  }
}
.trabajos-grid {
  margin: 7rem 0;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
  max-width: 44rem;
  margin-inline: auto;
}
@media (min-width: 768px) {
  .trabajos-grid {
    max-width: initial;
  }
}
.trabajos .filtros {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  transform: translateY(-50%);
  border-radius: 0.7rem;
  box-shadow: 0px 13px 22px -7px #b7d7d7;
  display: none;
}
.trabajos .filtros.mostrar {
  display: flex;
}
.trabajos .filtros .tags-contenedor {
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 3rem;
}
@media (min-width: 768px) {
  .trabajos .filtros .tags-contenedor {
    grid-template-columns: repeat(7, auto);
  }
}
.trabajos .filtros .tag-filtro {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: hsl(180deg, 52%, 96%);
  border-radius: 1rem;
}
.trabajos .filtros .tag-filtro p {
  color: hsl(180deg, 29%, 50%);
  padding: 0.7rem 1rem;
  font-weight: bold;
}
.trabajos .filtros .tag-filtro .btn-eliminar {
  height: 100%;
  background-color: hsl(180deg, 29%, 50%);
  background-image: url(../img/icon-remove.svg);
  background-repeat: no-repeat;
  background-position: center;
  width: 3rem;
  border-radius: 0%;
  transition: all 0.3s;
  border-radius: 0 1rem 1rem 0;
}
.trabajos .filtros .tag-filtro .btn-eliminar:hover {
  background-color: hsl(180deg, 14%, 20%);
}
.trabajos .filtros .eliminar-filtros {
  background: none;
  color: hsl(180deg, 8%, 52%);
  transition: all 0.3s;
}
.trabajos .filtros .eliminar-filtros:hover {
  color: hsl(180deg, 29%, 50%);
  text-decoration: underline;
}

.trabajo {
  background-color: #fff;
  padding: 0rem 2rem 2rem;
  border-radius: 0.7rem;
  box-shadow: 0px 8px 18px -7px #b7d7d7;
}
@media (min-width: 768px) {
  .trabajo {
    padding: 2.5rem 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.trabajo.new-featured {
  border-left: 0.5rem solid hsl(180deg, 29%, 50%);
}
@media (min-width: 768px) {
  .trabajo-header {
    display: flex;
    align-items: center;
    gap: 3rem;
  }
}
.trabajo-imagen {
  width: 8rem;
  height: auto;
  transform: translateY(-3.25rem);
}
@media (min-width: 768px) {
  .trabajo-imagen {
    width: 9.2rem;
    transform: translateY(0);
  }
}
.trabajo-info {
  padding-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .trabajo-info {
    padding: 0;
  }
}
.trabajo-new, .trabajo-featured {
  color: #fff;
  padding: 1rem;
  background-color: hsl(180deg, 29%, 50%);
  border-radius: 2rem;
  font-size: 1.5rem;
  text-transform: uppercase;
  margin: 0 1rem 0 2rem;
}
.trabajo-featured {
  background-color: hsl(180deg, 14%, 20%);
  margin: 0;
}
.trabajo-tags {
  padding-top: 1.5rem;
  border-top: 1px solid hsl(180deg, 8%, 52%);
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 1.5rem;
}
@media (min-width: 768px) {
  .trabajo-tags {
    border: none;
    grid-template-columns: repeat(2, auto);
  }
}
@media (min-width: 992px) {
  .trabajo-tags {
    padding: 0;
    display: flex;
    align-items: center;
    border: none;
  }
}